@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
body {
  margin: 0;
  color: #f2f2f2;
  background-color: rgb(36, 36, 36);
  font-family: 'Oswald', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.close-button{font-size:20px;position:absolute;top:15px;right:15px;cursor:pointer}

.spinner-overlay{height:50vh;display:flex;justify-content:center;align-items:center}.spinner-container{width:50px;height:50px;border:4px solid #e8e8e8;border-top-color:#6dc8d6;border-radius:50%;-webkit-animation-name:spin;animation-name:spin;transition:2s;animation:spin 1s ease-in-out infinite;-webkit-animation:spin 1s ease-in-out infinite}@keyframes spin{to{-webkit-transform:rotate(360deg)}}@-webkit-keyframes spin{to{-webkit-transform:rotate(360deg)}}

.graph{margin:100px 2vw;position:relative;width:76vw}@media only screen and (max-width: 600px){.graph{width:96vw}}.graph .switch-graph-button{position:absolute;top:60px;right:20px;z-index:4;height:30px;font-size:14px;cursor:pointer;background-color:#242424;border:1px solid;border-radius:3px;font-family:'Oswald';color:#d6d6d6;font-weight:400;outline:none;transition:0.3s ease-in-out}.graph .switch-graph-button:hover{color:#242424;background-color:#d6d6d6;transition:0.3s ease-in-out}

.pie{margin:50px 0;width:76vw;margin-left:2vw}@media only screen and (max-width: 600px){.pie{margin-left:12vw}}

.stats-grid{display:grid;grid-template-columns:repeat(2, minmax(150px, 1fr));grid-template-rows:repeat(4, minmax(110px, 1fr));padding-top:30px}.stats-grid .stat-field{min-width:100px}.stats-grid .stat-field:last-child{grid-column:1 / span 2}.stats-grid .stat-field .stat-name{color:#6dc8d6}.stats-grid .stat-field .stat-value{font-size:25px;margin:10px;border:1px solid rgba(109,200,214,0.1);padding:8px 10px;border-radius:15px}@media only screen and (max-width: 450px){.stats-grid .stat-field .stat-value{font-size:20px}}

.updated-at{margin-top:20px;margin-right:50px;text-align:right;font-size:12px;letter-spacing:1px}

.country-stats{background-color:#242424;position:fixed;z-index:3;width:80vw;margin-left:10vw;height:100%;border-radius:5px;transform:translateY(100%);transition:0.3s ease-in-out;overflow-Y:scroll;overflow-X:hidden}.country-stats::-webkit-scrollbar{width:8px}.country-stats::-webkit-scrollbar-track{border-radius:5px;background:#3f3f3f}.country-stats::-webkit-scrollbar-thumb{border-radius:5px;background:#888}.country-stats::-webkit-scrollbar-thumb:hover{background:#666}.country-stats.active{transform:translateY(0);transition:0.3s ease-in-out}@media only screen and (max-width: 600px){.country-stats{width:100vw;margin-left:0}}

.hamburger{margin-left:20px;position:fixed;z-index:5;top:10px;height:36px;width:25px;cursor:pointer}.hamburger div{margin:4px;height:4px;width:100%;background:#b5b5b5;border-radius:20%;transform:rotate(0deg);transition:.25s ease-in-out;opacity:1}.hamburger.open div{position:absolute}.hamburger.open :nth-child(1){top:6px;transform:rotate(135deg)}.hamburger.open :nth-child(2){transform:translateX(-200%);opacity:0.2}.hamburger.open :nth-child(3){top:6px;transform:rotate(-135deg)}

.categories-container{margin-top:50px;text-align:center}.categories-container p{margin:0}.categories-container .buttons{margin-top:0;padding:0;list-style:none;display:flex;justify-content:center}.categories-container .buttons :nth-child(1){color:#ef8828}.categories-container .buttons :nth-child(2){color:#ed2842}.categories-container .buttons :nth-child(3){color:#28ef64}.categories-container .buttons button{background-color:Transparent;border:none;font-family:'Oswald', sans-serif;letter-spacing:0.2mm;outline:none;font-size:14px;margin:8px;transition:0.3s;cursor:pointer}.categories-container .buttons button:after{display:block;content:'';padding:2px;border-bottom:solid 2px;transform:scaleX(0);transition:transform 250ms ease-in-out}.categories-container .buttons button.open:after{transform:scaleX(1)}.categories-container .buttons button:hover{transition:0.3s}.categories-container .buttons button:hover:after{transform:scaleX(1)}

.search-container{margin-top:40px}.search-container input{font-family:'Oswald', sans-serif;height:40px;width:200px;border-radius:5px;border:none;outline:none}.search-container .search-results{font-size:13px;margin-top:20px;display:flex;width:100%;height:100px;flex-direction:column;overflow-y:scroll;overflow-x:hidden;border-top:1px solid rgba(81,81,81,0.5);border-bottom:1px solid rgba(81,81,81,0.5)}.search-container .search-results::-webkit-scrollbar{width:8px}.search-container .search-results::-webkit-scrollbar-track{border-radius:5px;background:#3f3f3f}.search-container .search-results::-webkit-scrollbar-thumb{border-radius:5px;background:#888}.search-container .search-results::-webkit-scrollbar-thumb:hover{background:#666}.search-container .search-results .result:hover{color:#bababa;cursor:pointer}

.navigation{display:flex;flex-direction:column;align-items:center;list-style:none;padding:0;margin-top:100px}.navigation li{margin:2px;cursor:pointer;font-size:16px;transition:0.2s ease-in-out}.navigation li:hover{color:#6dc8d6;transition:0.2s ease-in-out}

.menu{font-size:14px;letter-spacing:0.2mm;position:fixed;z-index:4;top:0;height:100vh;width:300px;background-color:rgba(36,36,36,0.9);border-radius:0 10px 10px 0;transform:translateX(-100%);transition:0.2s ease-in-out;box-shadow:2px 2px 8px #303030}.menu.open{transform:translateX(0);transition:0.2s ease-in-out}.menu .footer{font-size:10px;letter-spacing:0.7mm;position:absolute;bottom:0;left:30%}

.circle{cursor:pointer;transform:translate(-50%, -50%);border-radius:50%}.totalConfirmedCases{fill:rgba(239,136,40,0.1);stroke:rgba(239,136,40,0.3)}.totalDeaths{fill:rgba(237,40,66,0.1);stroke:rgba(237,40,66,0.3)}.totalRecoveredCases{fill:rgba(40,239,100,0.1);stroke:rgba(40,239,100,0.3)}


.info-card{display:flex;flex-direction:column;align-items:center}.info-card .country-name{margin:0;padding-bottom:10px;border-bottom:1px solid rgba(242,242,242,0.2);width:75%}.info-card .description{margin-top:15px;font-size:13px;align-self:start}.info-card .description .category-container{text-align:start;display:flex;justify-content:flex-start;margin:5px}.info-card .description .category-container .new-info{position:absolute;right:10px;border:1px solid;border-radius:30%;padding:0 5px}.info-card .description .category-container .new-info.confirmed{color:#ef8828}.info-card .description .category-container .new-info.deaths{color:#ed2842}.info-card .description .category-container .new-info.recoverd{color:#28ef64}.info-card img{width:64px;height:auto}

.h{transition:0.3s}.mapboxgl-popup-content{width:180px;background-color:rgba(36,36,36,0.9)}.mapboxgl-popup-tip{display:none}


.stats-container,.stay-safe,.history{background-color:#242424;position:fixed;z-index:3;width:80vw;margin-left:10vw;height:100vh;border-radius:5px;transform:translateY(100%);transition:0.3s ease-in-out;overflow-Y:scroll;overflow-X:hidden}.stats-container::-webkit-scrollbar,.stay-safe::-webkit-scrollbar,.history::-webkit-scrollbar{width:8px}.stats-container::-webkit-scrollbar-track,.stay-safe::-webkit-scrollbar-track,.history::-webkit-scrollbar-track{border-radius:5px;background:#3f3f3f}.stats-container::-webkit-scrollbar-thumb,.stay-safe::-webkit-scrollbar-thumb,.history::-webkit-scrollbar-thumb{border-radius:5px;background:#888}.stats-container::-webkit-scrollbar-thumb:hover,.stay-safe::-webkit-scrollbar-thumb:hover,.history::-webkit-scrollbar-thumb:hover{background:#666}.stats-container.active,.stay-safe.active,.history.active{transform:translateY(0);transition:0.3s ease-in-out}@media only screen and (max-width: 600px){.stats-container,.stay-safe,.history{width:100vw;margin-left:0}}

.history{overflow-Y:scroll;overflow-X:hidden}.history::-webkit-scrollbar{width:8px}.history::-webkit-scrollbar-track{border-radius:5px;background:#3f3f3f}.history::-webkit-scrollbar-thumb{border-radius:5px;background:#888}.history::-webkit-scrollbar-thumb:hover{background:#666}

.stay-safe ul{padding:0 80px;text-align:left}.stay-safe ul li:nth-child(even){color:#6dc8d6}.stay-safe p{padding:50px}.stay-safe p:last-child{margin-top:100px;color:#6dc8d6}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

